<style scoped>
.table-body{top: 70px;}
.con-body{overflow:auto;}
.loginDet img{width: 100px;height: 100px;display:block;margin: 0 auto 20px auto;background:#eee;border: 1px solid #ccc;}
</style>
<template>
    <div class="main-wrap">
        <div class="main-box">
			<div class="con-body">
				<div style="width: 300px;">
					<Input v-model="searchKey" placeholder="请输入终端名、终端ID..."></Input>
				</div>
				<div class="table-body">
					<Table :columns="termCol" :data="_termList"></Table>
					<div class="tc mt-20 mb-20" v-if="nextPageShow"><Button type="primary" @click="nextPage" :loading="nextPageLoad">加载更多</Button></div>
				</div>
				<div class="table-footer">
					<Button type="primary" @click="openAeTerm">添加终端</Button>
					<Tooltip placement="top">
					<Button type="primary" @click="_export">导出</Button>
					<div slot="content">
						<p>导出只针对当前页面数据，如果要导出全部,</p>
						<p>请点击列表加载更多，然后再导出</p>
					</div>
					</Tooltip>
				</div>
			</div> 
        </div>
		<!--S 安装/编辑终端-->
        <Modal v-model="aeTermProp.show" :title="aeTermProp.isEdit?'编辑终端':'添加终端'" :width="400" :mask-closable="false">
            <Form ref="aeTermForm" :model="aeTermProp.propObj" :rules="aeTermRule" :label-width="100">
				<FormItem prop="robotName" label="终端名">
					<Input type="text" v-model="aeTermProp.propObj.robotName" placeholder="请输入内容..."></Input>
				</FormItem>
				<div v-if="!aeTermProp.isEdit">
					<FormItem prop="count" label="安装个数">
						<InputNumber :min="1" :max="30" v-model="aeTermProp.propObj.count" style="width: 100%;"></InputNumber>
					</FormItem>
					<FormItem prop="serverId" label="服务器">
						<Select v-model="aeTermProp.propObj.serverId">
							<Option v-for="(item,index) in serverList" :value="item._id" :key="index">{{item.name}}</Option>
						</Select>
					</FormItem>
					<FormItem prop="stype" label="终端类型">
					<RadioGroup v-model="aeTermProp.propObj.stype">
                        <Radio label="WX">微信</Radio>
                        <Radio label="QQ">QQ</Radio>
                    </RadioGroup>
				</FormItem>
				</div>
				<FormItem prop="platformId" label="平台选择">
					<Select v-model="aeTermProp.propObj.platformId">
						<Option v-for="(item,index) in platformList" :value="item._id" :key="index">{{item.name}}</Option>
					</Select>
				</FormItem>
				<FormItem prop="sock5On" label="SOCK5">
					<RadioGroup v-model="aeTermProp.propObj.sock5On">
                        <Radio :label="1">启用</Radio>
                        <Radio :label="0">关闭</Radio>
                    </RadioGroup>
				</FormItem>
			</Form>
            <div slot="footer">
				<Button type="primary" @click="smAeTerm" :loading="aeTermProp.btnLoad">确定</Button>
            </div>
        </Modal>
        <!--E 安装/编辑终端-->
		<!--S 登录详情-->
		<Modal v-model="loginDetProp.show" title="终端详情" :width="300" class="loginDet">
			<Spin fix v-if="loginDetProp.loading"></Spin>
			<div style="line-height: 30px;">
				<!--<img src="data:image/jpeg;base64," alt="">-->
				<img :src="loginDetProp.propObj.HeadimgUrl || '-/-'" alt="">
				<div>微信名：{{loginDetProp.propObj.Alias || '-/-'}}</div>
				<div>微信昵称：{{loginDetProp.propObj.Nickname || '-/-'}}</div>
				<div>微信ID：{{loginDetProp.propObj.Wxid || '-/-'}}</div>
				<div>终端ID：{{loginDetProp.propObj.robotId || '-/-'}}</div>
				<div>性别：{{loginDetProp.propObj.Sex || '-/-'}}</div>
				<div>地区：{{loginDetProp.propObj.City || '-/-'}}</div>
				<div>logsUrl: <a :href="loginDetProp.propObj.logsUrl || '-/-'" target="_blank">{{loginDetProp.propObj.logsUrl || '-/-'}}</a></div>
			</div>
			<div slot="footer"></div>
		</Modal>
		<!--E 登录详情-->
		<!--S 导出-->
		<Modal v-model="exportProp.show" title="导出" :width="300" class="loginDet">
			<Input type="textarea" v-model="exportProp.termNumList" readonly :rows="10" ></Input>
			<div slot="footer"></div>
		</Modal>
		<!--E 导出-->
		
    </div>
</template>
<script>
	import md5 from 'js-md5'
	var ws;
    export default {
		data: function(){
			return {
				opID: null,
				total: 0,
				current: 1,
				nextPageShow: false,
				nextPageLoad: false,
				exportProp:{
					show: false,
					termNumList: '',
				},
				searchKey: '',
				termCol: [
					{title: '终端ID',key: '_id'},
					{title: '终端名',key: 'name'},
					{title: '平台',key: 'platformName'},
					{title: '地区',key: 'area'},
					{title: '服务器IP',key: 'serverIp'},
					{title: '服务器名',key: 'serverName'},
					{title: '类型',key: 'stype'},
					{title: 'sock5状态',key: 'sock5On',
						render: (h, params) => {
							var _this = this;
							return h('div', {
								style: {
									color: params.row.sock5On == true?'#19be6b':'#ff9900'
								}
							}, params.row.sock5On == true?'已开启':'已关闭');
						}
					},
					{title: '操作',key: 'op',width: 240,align: 'center',
						render: (h, params) => {
                            var _this = this;
                            return h('div', [
								h('Button',{
                                    props: {
                                        type: 'ghost',
                                        size: 'small',
                                    },
                                    style: {
                                        marginRight: '5px'
                                    },
                                    on:{
                                        click: function(){
											_this.loginDetProp.show = true;
											_this.loginDet(params.row._id);
                                        }
                                    }
                                },'登录详情'),
								h('Button',{
                                    props: {
                                        type: 'ghost',
                                        size: 'small',
                                    },
                                    style: {
                                        marginRight: '5px'
                                    },
                                    on:{
                                        click: function(){
											_this.updateTerm(params.row);
                                        }
                                    }
                                },'更新'),
                                h('Button',{
                                    props: {
                                        type: 'ghost',
                                        size: 'small',
                                    },
                                    style: {
                                        marginRight: '5px'
                                    },
                                    on:{
                                        click: function(){
											_this.opID = params.row._id;
											_this.openAeTerm('edit',params.row);
                                        }
                                    }
                                },'编辑'),
								h('Button',{
                                    props: {
                                        type: 'error',
                                        size: 'small',
                                    },
                                    style: {
                                        marginRight: '5px'
                                    },
                                    on:{
                                        click: function(){
											_this.$Modal.confirm({
                                            title: '谨慎操作',
                                            content: '<p>确定删除么？</p>',
                                            loading: true,
                                            onOk: () => {
													_this.delTerm(params.row);
												}
											});
                                        }
                                    }
                                },'删除'),
                            ]);
                        }
					}
				],
				termList: [],
				aeTermProp: {
					isEdit: false,
					show: false,
					btnLoad: false,
					sock5List: [],
					propObj: {
						serverId: '',
						robotName: '',
						platformId: null,
						platformName: '',
						sock5On: 0,
						count: 1,
						stype: '',
					}
				},
				// 创建/编辑终端验证
				aeTermRule: {
					robotName: this.baseJs.textRule,
					count: this.baseJs.textRule,
					serverId: this.baseJs.selRule,
				},
				// 登录详情
				loginDetProp: {
					show: false,
					loading: false,
					propObj:{}
				}
			}
		},
		created: function(){
			var _this = this;
			_this.getTermList(1);
		},
		computed: {
			_termList(){
				var arr = this.termList.filter(item=>{
					return (item.name == this.searchKey) || (item._id == this.searchKey);
				})
				if(this.searchKey){
					return arr;
				}else{
					return this.termList;
				}
			},
			platformList(){
				return this.$store.state.platformList;
			},
			serverList(){
				var arr = [];
				this.$store.state.serverList.forEach(item=>{
					if(item.type == 'robot'){
						arr.push(item)
					}
				})
				return arr;
			},
			
		},
		methods: {
			//获取终端列表
			getTermList: function(page){
				var _this = this;
				if(page == 1){_this.current = 1;}
				_this.axios.get(`${_this.baseJs.getUrl}/robots/ls?page=${page}`).then(function(response){
					var result = response.data;
					if(result.result){
						var dataList = result.data || [];
						if(dataList.length < 30){
							_this.nextPageShow = false;
						}else{
							_this.nextPageShow = true;
						}
						if(page == 1){
							_this.termList = dataList;
						}else{
							dataList.forEach(item=>{
								_this.termList.push(item)
							})
						}
						_this.nextPageLoad = false;
					}else{
						_this.nextPageLoad = false;	
					}
				});
			},
			// 打开添加/编辑终端
			openAeTerm(type,obj){
				this.baseJs.resetForm(this,'aeTermForm');
				if(type == 'edit'){
					this.aeTermProp.isEdit = true;
					this.aeTermProp.propObj.robotName = obj.name;
					this.aeTermProp.propObj.platformId = obj.platformId;
					this.aeTermProp.propObj.sock5On = obj.sock5On?1:0;
				}else{
					this.aeTermProp.isEdit = false;
				}
				this.aeTermProp.show = true;
			},
			// 提交添加/编辑终端
			smAeTerm(){
				 var _this = this;
				_this.$refs['aeTermForm'].validate((valid) => {
                    if (valid) {
                        _this.aeTermProp.btnLoad = true;
						var sendData,url;
						_this.platformList.forEach(item=>{
							item._id == _this.aeTermProp.propObj.platformId?_this.aeTermProp.propObj.platformName = item.name:'';
						})
						if(_this.aeTermProp.isEdit){
							url = '/robots/update';
							sendData = {
								_id         : _this.opID,
								name        : _this.aeTermProp.propObj.robotName,
								platformName: _this.aeTermProp.propObj.platformName,
								platformId  : _this.aeTermProp.propObj.platformId,
								sock5On     : _this.aeTermProp.propObj.sock5On
							}	
						}else{
							url = '/remote/robot/setup';
							sendData = _this.baseJs.copyObj(_this.aeTermProp.propObj);	
						}
						_this.axios.post(`${_this.baseJs.postUrl}${url}`,sendData).then(function(response){
							var result = response.data
							if(result.result){
								var dataList = result.data;
								_this.getTermList(1);
								_this.aeTermProp.btnLoad = false;
								_this.aeTermProp.show = false;
								_this.$Message.success('操作成功!');
							}else{
								_this.aeTermProp.btnLoad = false;
								_this.$Message.error(result.msg);
							}
						})
					} else {
                        _this.$Message.error('请完善表单');
                    }
				})
			},
			// 删除终端
			delTerm(obj){
				var _this = this;
				var sendData = {
					robotId: obj._id,
					serverId: obj.serverId,
					stype: obj.stype
				}
				_this.axios.post(`${_this.baseJs.postUrl}/remote/robot/delete`,sendData).then(function(response){
					var result = response.data
					if(result.result){
						_this.$Message.success('操作成功!');
						_this.getTermList(1);
						_this.$Modal.remove();
					}else{
						_this.$Modal.remove();
						_this.$Message.error(result.data);
					}
				})
			},
			// 更新终端
			updateTerm(obj){
				var _this = this;
				var sendData = {
					robotId: obj._id,
					serverId: obj.serverId,
					stype: obj.stype
				};
				_this.$Spin.show();
				_this.axios.post(`${_this.baseJs.postUrl}/remote/robot/update`,sendData).then(function(response){
					var result = response.data
					if(result.result){
						_this.$Spin.hide();
						_this.$Message.success('操作成功!');
					}else{
						_this.$Spin.hide();
						_this.$Message.error(result.data);
					}
				})
			},
			// 登录详情
			loginDet(id){
				var sendData = {}
				var _this = this;
				_this.axios.get(`${_this.baseJs.postUrl}/online/robots/${id}/detail`,sendData).then(function(response){
					var result = response.data
					if(result.result){
						var dataList = JSON.parse(result.data.data);
						var exdata = JSON.parse(result.data.exdata);
						_this.loginDetProp.propObj = dataList;
						_this.loginDetProp.loading = false;
						_this.loginDetProp.propObj.logsUrl = exdata.logsUrl;
					}else{
						_this.loginDetProp.loading = false;
						_this.$Message.error(result.msg);
					}
				})				
			},
			// 加载更多
			nextPage(){
				this.current+=1;
				this.nextPageLoad = true;
				this.getTermList(this.current);
			},
			// 导出
			_export(){
				var str = '';
				this.exportProp.termNumList = '';
				this._termList.forEach(item=>{
					str+=item._id+'\n'
				})
				this.exportProp.termNumList = str;
				this.exportProp.show = true;
			}
		}
    }
</script>


