export default {
	// wsUrl: "ws://47.52.229.51:7200/test/ws/0120508cd011000120808031086a09ab50212040807100012",
	masterIP: "http://120.79.20.53",
	postUrl: "/master",
	getUrl: "/master",
	// postUrl: "http://master.sogame.co:8081/master",
	// getUrl: "http://master.sogame.co:8081/master",
	// 时间戳格式化
	formatTime:  function (timestamp) {
		var newDate = new Date(timestamp);
		var date = {
			"M+": newDate.getMonth() + 1,
			"d+": newDate.getDate(),
			"h+": newDate.getHours(),
			"m+": newDate.getMinutes(),
			"s+": newDate.getSeconds(),
			"q+": Math.floor((newDate.getMonth() + 3) / 3),
			"S+": newDate.getMilliseconds()
		};
		var format = 'yyyy-MM-dd hh:mm:ss';
		if (/(y+)/i.test(format)) {
			format = format.replace(RegExp.$1, (newDate.getFullYear() + '').substr(4 - RegExp.$1.length));
		}
		for (var k in date) {
			if (new RegExp("(" + k + ")").test(format)) {
				format = format.replace(RegExp.$1, RegExp.$1.length == 1
					? date[k] : ("00" + date[k]).substr(("" + date[k]).length));
			}
		}
		return format;
	},
	// 判断是否全为空格 
	isEmptyStr: function(e){
		if(e.replace(/\s/g,'').length<1){
			return true;
		}else{
			return false;
		}
	},
	//重置表单
	resetForm: function(_this,name){
		_this.$refs[name].resetFields();
	},
	// 生成UUID
	uuid: function() {
		return 'xxxxxxxx-xxxx-4xxx-yxxx-xxxxxxxxxxxx'.replace(/[xy]/g, function(c) {
			var r = Math.random()*16|0, v = c == 'x' ? r : (r&0x3|0x8);
			return v.toString(16);
		});
	},
	// 随机端口号(6535~8000)
	randomWebPort: function(s,e){
		var n = e - s;
		return Math.floor(Math.random() * n + s);
	},
	// 验证是否全为空字符串
	vBlank: function(rule, value, callback){
		if (value.replace(/\s/g,'').length < 1) {
			callback(new Error('不能为空！'));
		} else {
			callback();
		}
	},
	textRule: [
		{ required: true, pattern: /\S/g, message: '不能为空!', trigger: 'blur' }
	],
	selRule: [
		{ required: true, message: '不能为空!', trigger: 'change' }
	],
	// 深拷贝对象
	copyObj: function(obj){
		if(typeof obj != 'object'){
			return obj;
		}
		var newobj = {};
		for ( var attr in obj) {
			newobj[attr] = this.copyObj(obj[attr]);
		}
		return newobj;
	},
};


