<style scoped>
    .layout-logo-left{
        height: 60px;
        line-height: 60px;
        font-size: 20px;
        font-weight: bold;
        text-align: center;
        color: #fff;
    }
    .ivu-menu-dark.ivu-menu-vertical .ivu-menu-submenu .ivu-menu-item-active, .ivu-menu-dark.ivu-menu-vertical .ivu-menu-submenu .ivu-menu-item-active:hover{
        background: #19be6b !important;
    }
    .ivu-menu-item>i{
        vertical-align: middle;
    }
</style>
<template>
    <div width="160px" class="layout-menu-left">
        <div class="layout-logo-left">平台控制管理</div>
        <Menu :active-name="$route.path" width="180px" @on-select="selMenu" ref="sideMenu" theme="dark">
            <!--<MenuItem name="/controllerManage"><Icon type="stats-bars"></Icon>控制器</MenuItem>-->
            <MenuItem name="/controllerTypeManage"><Icon type="stats-bars"></Icon>地区管理</MenuItem>
            <MenuItem name="/serverManage"><Icon type="stats-bars"></Icon>服务器管理</MenuItem>
            <MenuItem name="/platformManage"><Icon type="stats-bars"></Icon>平台管理</MenuItem>
            <MenuItem name="/termManage"><Icon type="stats-bars"></Icon>终端管理</MenuItem>
            <!--<MenuItem name="/versionsManage"><Icon type="stats-bars"></Icon>版本管理</MenuItem>-->
            <MenuItem name="/sockManage"><Icon type="stats-bars"></Icon>SOCK5管理</MenuItem>
        </Menu>
    </div>
</template>
<script>
    export default {
        name: 'sidebar',
        data: function(){
            return {
                activeName: '/controllerManage',
            }
        },
        created () {
            // this.update();
        },
        methods: {
            selMenu: function(name){
                this.$router.push(name)
            },
            // update: function(route) {
            //     const path = route ? route.path : this.$route.path
            //     if(path=='/'){return;}
            //     this.$set(this, 'activeName', path)
            // },
        },
    }
</script>



