import login from '../components/page/login.vue'
import home from '../components/common/home.vue'
import controllerManage from '../components/page/controllerManage.vue'
import controllerTypeManage from '../components/page/controllerTypeManage.vue'
import serverManage from '../components/page/serverManage/serverManage.vue'
import serverList from '../components/page/serverManage/serverList.vue'
import platformManage from '../components/page/platformManage.vue'
import termManage from '../components/page/termManage.vue'
import controllerDetManage from '../components/page/controllerDetManage.vue'
import apiTest from '../components/page/apiTest.vue'
import log from '../components/page/log.vue'
// import versionsManage from '../components/page/versionsManage.vue'
import sockManage from '../components/page/sockManage.vue'
export default [
	{
		path: '/',
		redirect: "/login"
	},
	{
		path: '/login',
		name: 'login',
		component: login
	},
	{
		path: '/home',
		component: home,
		children: [
			{
				path: '/',
				redirect: '/controllerTypeManage'
			},
			{
				path: '/controllerManage',
				name: 'controllerManage',
				component: controllerManage
			},
			{
				path: '/controllerTypeManage',
				name: 'controllerTypeManage',
				component: controllerTypeManage
			},
			{
				path: '/serverManage',
				name: 'serverManage',
				component: serverManage
			},
			{
				path: '/serverList',
				name: 'serverList',
				component: serverList
			},
			{
				path: '/platformManage',
				name: 'platformManage',
				component: platformManage
			},
			{
				path: '/termManage',
				name: 'termManage',
				component: termManage
			},
			// {
			// 	path: '/versionsManage',
			// 	name: 'versionsManage',
			// 	component: versionsManage
			// },
			{
				path: '/sockManage',
				name: 'sockManage',
				component: sockManage
			}
		]
	}
]


