<template>
    <div>
        <Spin fix v-if="loading"></Spin>
        <div class="table-box">
            <Table :columns="serverCol" :data="robotList"></Table>
        </div>
        <div class="table-footer">
            <Row>
                <Col span="24">
                    <Button type="primary" @click="openAeServerForm">添加服务器</Button>
                </Col>
            </Row>
        </div>
        <!--S 创建服务器-->
		<Modal v-model="aeServerProp.show" :title="aeServerProp.isEdit?'编辑服务器':'创建服务器'" :width="400" :mask-closable="false">
			<Form ref="aeServerForm" :model="aeServerProp.propObj" :rules="aeServerRule" :label-width="100">
				<FormItem prop="name" label="服务器名">
					<Input type="text" v-model="aeServerProp.propObj.name" placeholder="请输入内容..."></Input>
				</FormItem>
				<FormItem prop="ip" label="服务器IP">
					<Input type="text" v-model="aeServerProp.propObj.ip" placeholder="请输入内容..."></Input>
				</FormItem>
				<FormItem prop="rootUsr" label="登录账号">
					<Input type="text" v-model="aeServerProp.propObj.rootUsr" placeholder="请输入内容..."></Input>
				</FormItem>
				<FormItem prop="rootPwd" label="登录密码">
					<Input type="password" v-model="aeServerProp.propObj.rootPwd" placeholder="请输入内容..."></Input>
				</FormItem>
				<FormItem prop="privateKey" label="登录私钥">
					<Input type="textarea" v-model="aeServerProp.propObj.privateKey" placeholder="请输入内容..."></Input>
				</FormItem>
				<FormItem prop="area" label="服务器地区">
					<Input type="text" v-model="aeServerProp.propObj.area" placeholder="请输入内容..."></Input>
				</FormItem>
				<FormItem prop="type" label="服务器类型">
					<RadioGroup v-model="aeServerProp.propObj.type">
                        <Radio label="robot">终端服务器</Radio>
                        <Radio label="controller">控制器服务器</Radio>
                        <Radio label="gameapi">游戏API</Radio>
                    </RadioGroup>
				</FormItem>
			</Form>
			<div slot="footer">
				<Button type="primary" @click="smAeServer" :loading="aeServerProp.btnLoad">提交</Button>
			</div>
		</Modal>
		<!--E 创建服务器-->
        <!--S 安装终端-->
        <Modal v-model="aeTermProp.show" title="安装终端" :width="400" :mask-closable="false">
            <Form ref="aeTermForm" :model="aeTermProp.propObj" :rules="aeTermRule" :label-width="100">
				<FormItem prop="robotName" label="终端名">
					<Input type="text" v-model="aeTermProp.propObj.robotName" placeholder="请输入内容..."></Input>
				</FormItem>
				<div v-if="!aeTermProp.isEdit">
					<FormItem prop="count" label="安装个数">
						<InputNumber :min="1" :max="30" v-model="aeTermProp.propObj.count" style="width: 100%;"></InputNumber>
					</FormItem>
					<FormItem prop="serverId" label="服务器">
						<Select v-model="aeTermProp.propObj.serverId" disabled>
							<Option v-for="(item,index) in serverList" :value="item._id" :key="index">{{item.name}}</Option>
						</Select>
					</FormItem>
					<FormItem prop="stype" label="终端类型">
                        <RadioGroup v-model="aeTermProp.propObj.stype">
                            <Radio label="WX">微信</Radio>
                            <Radio label="QQ">QQ</Radio>
                        </RadioGroup>
                    </FormItem>
				</div>
				<FormItem prop="platformId" label="平台选择">
					<Select v-model="aeTermProp.propObj.platformId">
						<Option v-for="(item,index) in platformList" :value="item._id" :key="index">{{item.name}}</Option>
					</Select>
				</FormItem>
				<FormItem prop="sock5On" label="SOCK5">
					<RadioGroup v-model="aeTermProp.propObj.sock5On">
                        <Radio :label="1">启用</Radio>
                        <Radio :label="0">关闭</Radio>
                    </RadioGroup>
				</FormItem>
			</Form>
            <div slot="footer">
                <Button type="primary" @click="smAeTerm" :loading="aeTermProp.btnLoad">确定</Button>
            </div>
        </Modal>
        <!--E 安装终端-->
        <!--S 更新终端-->
        <Modal v-model="updateTermProp.show" title="更新终端" :width="400" :mask-closable="false">
            <Form :model="updateTermProp" :label-width="100">
                <FormItem prop="stype" label="终端类型">
                    <RadioGroup v-model="updateTermProp.stype">
                        <Radio label="WX">微信</Radio>
                        <Radio label="QQ">QQ</Radio>
                    </RadioGroup>
                </FormItem>
            </Form>
            <div slot="footer"><Button type="primary" @click="updateTerm" :loading="updateTermProp.btnLoad">确定</Button></div>
        </Modal>
        <!--E 更新终端-->
    </div>
</template>

<script>
export default {
    name: 'name',
    data: function() {	
        return {
            opID: null,
            loading: false,
            serverCol: [
                {title: 'ID',key: '_id'},
                {title: '服务器名称',key: 'name'},
                {title: '地区',key: 'area'},
                {title: '服务器IP',key: 'ip'},
                {
                    title: '操作',key: 'op',width: 300,align: 'center',
                    render: (h, params) => {
                        var _this = this;
                        return h('div', [
                            h('Button',{
                                props: {
                                    type: 'ghost',
                                    size: 'small',
                                },
                                style: {
                                    marginRight: '5px'
                                },
                                on:{
                                    click: function(){
                                        _this.openAeTerm('add',params.row);
                                    }
                                }
                            },'安装终端'),
                            h('Button',{
                                props: {
                                    type: 'ghost',
                                    size: 'small',
                                },
                                style: {
                                    marginRight: '5px'
                                },
                                on:{
                                    click: function(){
                                        _this.updateTermProp.serverId = params.row._id;
                                        _this.updateTermProp.show = true;
                                    }
                                }
                            },'更新'),
                            h('Button',{
                                props: {
                                    type: 'ghost',
                                    size: 'small',
                                },
                                style: {
                                    marginRight: '5px'
                                },
                                on:{
                                    click: function(){
                                        _this.opID = params.row._id;
                                        _this.openAeServerForm('edit',params.row);
                                    }
                                }
                            },'编辑'),
                            h('Button',{
                                props: {
                                    type: 'error',
                                    size: 'small',
                                },
                                on:{
                                    click: function(){
                                        _this.opID = params.row._id;
                                        _this.$Modal.confirm({
                                            title: '谨慎操作',
                                            content: '<p>确定删除么？</p>',
                                            loading: true,
                                            onOk: () => {
                                                _this.delServer(_this.opID);
                                            }
                                        });
                                    }
                                }
                            },'删除')
                        ]);
                    }
                }
            ],
            aeServerProp: {
                show: false,
                btnLoad: false,
                isEdit: false,
                propObj: {
                    type: '',
                    ip: '',
                    name: '',
                    area: '',
                    rootUsr: '',
                    rootPwd: '',
                    privateKey: '',
                    installed: false
                }
            },
            aeServerRule: {
                name: this.baseJs.textRule,
                ip: this.baseJs.textRule,
                rootUsr: this.baseJs.textRule,
                area: this.baseJs.textRule,
            },
            aeTermProp: {
                isEdit: false,
                show: false,
                btnLoad: false,
                sock5List: [],
                propObj: {
                    serverId: '',
                    robotName: '',
                    platformId: null,
                    platformName: '',
                    sock5On: 0,
                    count: 1,
                    stype: '',

                }
            },
            // 创建/编辑终端验证
            aeTermRule: {
                robotName: this.baseJs.textRule,
                count: this.baseJs.textRule,
                serverId: this.baseJs.selRule,
            },
            // 更新终端属性
            updateTermProp: {
                show: false,
                btnLoad: false,
                serverId: '',
                stype: 'WX',
            },
        }
    },
    computed: {
        platformList(){
				return this.$store.state.platformList;
			},
        robotList(){
            var arr = this.serverList.filter(item=>{
                return item.type == 'robot'
            })
            return arr;
        },
        serverList(){
            return this.$store.state.serverList;
        },
    },
    methods: {
        // 打开添加/编辑服务器
        openAeServerForm: function(edit,obj){
            var _this = this;
            _this.$refs['aeServerForm'].resetFields();
            if(edit == 'edit'){
                this.aeServerProp.isEdit = true;
                _this.aeServerProp.propObj.name = obj.name;
                _this.aeServerProp.propObj.ip = obj.ip;
                _this.aeServerProp.propObj.rootUsr = obj.rootUsr;
                _this.aeServerProp.propObj.rootPwd = obj.rootPwd;
                _this.aeServerProp.propObj.privateKey = obj.privateKey;
                _this.aeServerProp.propObj.type = obj.type;
                _this.aeServerProp.propObj.area = obj.area;
            }else{
                this.aeServerProp.isEdit = false;
            }
            this.aeServerProp.show = true;
        },
        // 提交添加/编辑服务器
        smAeServer: function(){
            var _this = this;
            _this.$refs['aeServerForm'].validate((valid) => {
                if (valid) {
                    _this.aeServerProp.btnLoad = true;
                    var url = '';
                    var sendData = _this.baseJs.copyObj(_this.aeServerProp.propObj);
                    if(_this.aeServerProp.isEdit){
                        sendData._id = _this.opID;
                        url = '/servers/update'
                    }else{
                        url = '/servers/create'
                    }
                    _this.axios.post(`${_this.baseJs.postUrl}${url}`,sendData).then(function(response){
                        var result = response.data;
                        if(result.result){
                            _this.aeServerProp.btnLoad = false;
                            _this.aeServerProp.show = false;
                            _this.$Message.success('操作成功！');
                            _this.$store.dispatch('getServerList');
                        }else{
                            _this.aeServerProp.btnLoad = false;
                            _this.$Message.error(result.msg);
                        }
                    });
                } else {
                    _this.$Message.error('请完善表单');
                }
            })
        },
        // 删除服务器
        delServer: function(id){
            var _this = this;
            var sendData = {_id: id}
            _this.axios.post(`${_this.baseJs.postUrl}/servers/delete`,sendData).then(function(response){
                var result = response.data;
                if(result.result){
                    _this.$Modal.remove();
                    _this.$Message.success('操作成功！');
                    _this.$store.dispatch('getServerList');
                }else{
                    _this.$Modal.remove();
                    _this.$Message.error(result.data);
                }
            });
        },
        // 打开安装终端
        openAeTerm(type,obj){
            this.baseJs.resetForm(this,'aeTermForm');
            if(type == 'edit'){
                this.aeTermProp.isEdit = true;
                this.aeTermProp.propObj.robotName = obj.name;
                this.aeTermProp.propObj.platformId = obj.platformId;
                this.aeTermProp.propObj.sock5On = obj.sock5On?1:0;
            }else{
                this.aeTermProp.propObj.serverId = obj._id;
                this.aeTermProp.isEdit = false;
            }
            this.aeTermProp.show = true;
        },
        // 提交安装终端
        smAeTerm(){
            var _this = this;
            _this.$refs['aeTermForm'].validate((valid) => {
                if (valid) {
                    _this.aeTermProp.btnLoad = true;
                    var sendData,url;
                    _this.platformList.forEach(item=>{
                        item._id == _this.aeTermProp.propObj.platformId?_this.aeTermProp.propObj.platformName = item.name:'';
                    })
                    if(_this.aeTermProp.isEdit){
                        url = '/robots/update';
                        sendData = {
                            _id         : _this.opID,
                            platformName: _this.aeTermProp.propObj.platformName,
                            platformId  : _this.aeTermProp.propObj.platformId,
                            sock5On     : _this.aeTermProp.propObj.sock5On
                        }	
                    }else{
                        url = '/remote/robot/setup';
                        sendData = _this.baseJs.copyObj(_this.aeTermProp.propObj);	
                    }
                    _this.axios.post(`${_this.baseJs.postUrl}${url}`,sendData).then(function(response){
                        var result = response.data
                        if(result.result){
                            var dataList = result.data;
                            // _this.getTermList(_this.current);
                            _this.aeTermProp.btnLoad = false;
                            _this.aeTermProp.show = false;
                            _this.$Message.success('操作成功!');
                        }else{
                            _this.aeTermProp.btnLoad = false;
                            _this.$Message.error(result.msg);
                        }
                    })
                } else {
                    _this.$Message.error('请完善表单');
                }
            })
        },
        // 更新终端
        updateTerm(){
            var _this = this;
            _this.updateTermProp.btnLoad = true;
            var sendData = {
                serverId: _this.updateTermProp.serverId,
                stype: _this.updateTermProp.stype
            }
            _this.axios.post(`${_this.baseJs.postUrl}/remote/robot/update`,sendData).then(function(response){
                var result = response.data
                if(result.result){
                    _this.updateTermProp.show = false;
                    _this.updateTermProp.btnLoad = false;
                    _this.$Message.success('更新成功');
                }else{
                    _this.updateTermProp.btnLoad = false;
                    _this.$Message.error(result.data);
                }
            })
        },
    }
}
</script>

<style scoped>
.table-box{
    position: absolute;
    top: 0;
    bottom: 70px;
    right: 0;
    left: 0;
    overflow: auto;
}
</style>


