<style scoped>
	.content-con{
		position: relative;
		width: 100%;
		height: 100%;
		position: relative;
		background: #495060;
		background-size: cover;
	}
	.login-logo{
		width: 280px;
		height: 60px;
		color: #ff9900;
		font-size: 20px;
		text-align: center;
	}
	.login-box{
		position: absolute;
		top: 50%;
		left: 50%;
		transform: translate(-50%, -50%);
		background: #fff;
		padding: 20px;
		box-shadow: 10px 10px 14px 0 rgba(0,0,0,0.30);
		border-radius: 4px;
	}
	.authCodeImg{
		float:right;
		width: 80px;
		height: 44px;
		vertical-align: middle;
		border-radius: 4px;
		overflow: hidden;
		cursor: pointer;
		background: #fff;
	}
	.login-box >>> .ivu-input{
		height: 44px;
	}
</style>
<template>
	<div class="content-con login">
		<div class="login-box">
			<div class="login-logo">平台控制中心</div>
			<Form ref="loginInfo" :model="loginInfo" :rules="ruleInline">
				<FormItem prop="user">
					<Input type="text" v-model="loginInfo.user" placeholder="请输入用户名">
						<Icon type="android-person" slot="prepend" size="18"></Icon>
					</Input>
				</FormItem>
				<FormItem prop="password">
					<Input type="password" v-model="loginInfo.password" placeholder="请输入密码"  @keyup.native.enter="smLogin('loginInfo')">
						<Icon type="locked"  slot="prepend" size="18"></Icon>
					</Input>
				</FormItem>
				<FormItem>
					<Button type="primary" @click="smLogin('loginInfo')" long size="large" :loading="loading">登录</Button>
				</FormItem>
			</Form>
		</div>
	</div>
</template>
<script>
	export default {
		data () {
			return {
				loading: false,
				loginInfo: {
					user: '',
					password: '',
					verif: ''

				},
				ruleInline: {
					user: [
						{ required: true, message: '请输入用户名', trigger: 'blur' }
					],
					password: [
						{ required: true, message: '请输入密码', trigger: 'blur' },
					],
				}
			}
		},
		created: function(){},
		methods: {
			smLogin(name) {
				var _this = this;
				_this.$refs[name].validate((valid) => {
					if (valid) {
						var sendData = {
							username: _this.loginInfo.user,
							password: _this.loginInfo.password
						}
						_this.axios.post(`${_this.baseJs.postUrl}/login`,sendData).then(function(response){
							var result = response.data
							if(result.result){
								sessionStorage.setItem('accessToken', true);
								_this.$router.push('/home');
							}else{
								_this.$Message.error(result.data);
							}
						})						
					} else {
						_this.$Message.error('请完善表单！');
					}
				})
			}
		}
	}
</script>


