<style scoped>
.con-body{overflow:auto;}
</style>
<template>
    <div class="main-wrap">
        <div class="main-box">
			<div class="con-body">
				<div class="table-body">
					<Table :columns="contCol" :data="contList"></Table>
				</div>
				<div class="table-footer">
					<Row>
						<Col span="8">
							<Button type="primary" @click="openAeContForm">创建控制器</Button>
						</Col>
						<Col span="16" style="text-align:right;">
							<Page :total="total" :current="current" @on-change="pageGo"></Page>
						</Col>
					</Row>
				</div>
			</div> 
        </div>
		<!--S 创建控制器-->
		<Modal v-model="aeContProp.show" title="创建/编辑控制器" :width="400" :mask-closable="false">
			<Form ref="aeContForm" :model="aeContProp.propObj" :rules="aeContRule" :label-width="100">
				<FormItem prop="name" label="控制器名称">
					<Input type="text" v-model="aeContProp.propObj.name" placeholder="请输入内容..."></Input>
				</FormItem>
				<FormItem prop="serverIP" label="控制器IP">
					<Select v-model="aeContProp.propObj.serverIP" @on-change="contIPChange" :label-in-value="true">
						<Option v-for="(item,index) in $store.state.serverList" :value="item._id" :key="index">{{item.name}}</Option>
					</Select>
				</FormItem>
				<FormItem prop="controlTypeId" label="控制器类型">
					<Select v-model="aeContProp.propObj.controlTypeId">
						<Option v-for="(item,index) in $store.state.contTypeList" :value="item._id" :key="index">{{item.name}}</Option>
					</Select>
				</FormItem>
				<FormItem prop="versionId" label="版本号">
					<Select v-model="aeContProp.propObj.versionId" @on-change="versionChange"  :label-in-value="true">
						<Option v-for="(item,index) in $store.state.versionList" :value="item._id" :key="index">{{item.name}}</Option>
					</Select>
				</FormItem>
				<FormItem prop="redisAddr" label="redis地址">
					<Input type="text" v-model="aeContProp.propObj.redisAddr" placeholder="请输入内容..."></Input>
				</FormItem>
				<FormItem prop="redisPwd" label="redis密码">
					<Input type="text" v-model="aeContProp.propObj.redisPwd" placeholder="请输入内容..."></Input>
				</FormItem>
			</Form>
			<div slot="footer">
				<Button type="primary" @click="smAeController" :loading="aeContProp.btnLoad">提交</Button>
			</div>
		</Modal>
		<!--E 创建控制器-->
    </div>
</template>
<script>
	import md5 from 'js-md5'
    export default {
		data: function(){
			return {
				firstContIP: '',
				firstContID: null,
				opID: null,
				current: 1,
				total: 0,
				contCol: [
					{title: '控制器ID',key: '_id'},
					{title: '控制器名称',key: 'name'},
					{title: '控制器类型',key: 'contType'},
					{title: '控制器IP',key: 'serverIp'},
					{title: '版本号',key: 'version'},
					{title: '操作',key: 'op',width: 290,align: 'center',
						render: (h, params) => {
                            var _this = this;
                            return h('div', [
								h('Button',{
                                    props: {type: 'ghost',size: 'small',icon: 'refresh'},
                                    style: {
                                        marginRight: '5px'
                                    },
                                    on:{
                                        click: function(){
											sessionStorage.setItem('controlID', params.row.id);
											sessionStorage.setItem('controlIP', params.row.controlIP);
											_this.$router.push('/controllerDetManage');
                                        }
                                    }
                                },'管理'),
								h('Button',{
                                    props: {type: 'ghost',size: 'small',icon: 'refresh',disabled: params.row.id == 1},
                                    style: {
                                        marginRight: '5px'
                                    },
                                    on:{
                                        click: function(){
											_this.updateCont(params.row);
                                        }
                                    }
                                },'更新'),
                                h('Button',{
                                    props: {
										disabled: params.row.id == 1,
										type: 'error',
										size: 'small',
										icon: 'trash-a'
									},
                                    on:{
                                        click: function(){
											_this.opID = params.row.id;
											_this.$Modal.confirm({
												title: '谨慎操作',
												content: '<p>确定删除么？</p>',
												// loading: true,
												onOk: () => {
													_this.delCont(params.row);
												}
											});
                                        }
                                    }
                                },'删除'),
                            ]);
                        }
					}
				],
				contList: [],
				aeContRule: {
					name: this.baseJs.textRule,
				},
				aeContProp: {
					show: false,
					btnLoad: false,
					isEdit: false,
					controlTypeList: [],
					versionList: [],
					version: '',
					serverList: [],
					serverIP: '',
					controlIP: '',
					propObj: {
						cid: '',
						name: '',
						controlTypeId: null,
						accountName: '',
						psw: '',
						privatekey: '',
						versionId: null,
						serverIP: null,
						masterAddr: '',
						redisAddr: '',
						redisPwd: '',
						scryptUrl: 'http://git.sogame.co/zhangyuge/downloads/raw/master/controller_scrypt',
					}
				},
				wsUrl: '',
			}
		},
		created: function(){
			var _this = this;
			_this.getContList();
		},
		methods: {
			// 获取控制器列表
			getContList: function(){
				var _this = this;
				_this.axios.get(`${_this.baseJs.postUrl}/controllers/ls`).then(function(response){
					var result = response.data;
					if(result.result){
						var dataList = result.data || [];
						var tempData = [];
						dataList.forEach(function(item){
							tempData.push({
								_id: item._id,
								name: item.name,
								name: item.name,
								contType: item.controller_type?item.controller_type.name:'',
								serverIp: item.serverIp,
								version: item.version?item.version.name:'',
							})
						})
						_this.contList = tempData;
					}else{
						_this.$Message.error(result.data);
					}
				})
			},
			// 打开添加/编辑控制器
			openAeContForm: function(edit,obj){
				var _this = this;
				_this.$refs['aeContForm'].resetFields();
				if(edit == 'edit'){
					this.aeContProp.isEdit = true;
					_this.aeContProp.propObj.name = obj.name;
					_this.aeContProp.propObj.controlIP = obj.controlIP;
					_this.aeContProp.propObj.accountName = obj.accountName;
					_this.aeContProp.propObj.psw = obj.psw;
					_this.aeContProp.propObj.privatekey = obj.privatekey;
					_this.aeContProp.propObj.controlTypeId = obj.controlTypeId;
					_this.aeContProp.propObj.versionId = obj.versionId;
				}else{
					this.aeContProp.isEdit = false;
				}
				this.aeContProp.show = true;
			},
			// 提交添加控制器
			smAeController: function(){
				var _this = this;
				_this.$refs['aeContForm'].validate((valid) => {
                    if (valid) {
						_this.aeContProp.btnLoad = true;
						_this.$Spin.show();
						var uuid = md5(_this.baseJs.uuid());
						var masterAddr = _this.baseJs.masterIP;
						_this.aeContProp.propObj.cid = uuid;
						_this.aeContProp.propObj.masterAddr = masterAddr;
						var tempData = _this.baseJs.copyObj(_this.aeContProp.propObj)
						tempData.controlIP = _this.aeContProp.controlIP;
						tempData.version = _this.aeContProp.version;
						var ws = new WebSocket(_this.wsUrl);
						ws.onopen = function () {
							ws.send(_this.wsSendData(tempData,207));
						};
						ws.onmessage = function (result) {
							var data = JSON.parse(result.data);
							if(data.code == '207'){
								if(data.data.indexOf('大吉大利')>-1){
									var url = '';
									var sendData = _this.baseJs.copyObj(_this.aeContProp.propObj);
									_this.axios.post(`${_this.baseJs.postUrl}/control/add`,sendData).then(function(response){
										var result = response.data;
										if(result.result){
											_this.$Spin.hide();
											_this.aeContProp.btnLoad = false;
											_this.aeContProp.show = false;
											_this.$Message.success('操作成功！');
											_this.getContList(_this,_this.current);
										}else{
											_this.aeContProp.btnLoad = false;
											_this.$Message.error(result.msg);
										}
									});
									ws.close();
								}else{
									_this.$Spin.hide();
									_this.aeContProp.btnLoad = false;
									_this.$Message.error('操作失败，请重试！');
									ws.close();
								}
							}
						};
                    } else {
                        _this.$Message.error('请完善表单');
                    }
                })
			},
			// websocket发送数据格式化
			wsSendData: function(obj,code){
				return JSON.stringify({
							code: code,
							robotId: '',
							data: {
								targetAddr: obj.controlIP,
								privateKey: obj.privatekey,
								rootUsr: obj.accountName,
								rootPwd: obj.psw,
								Servers: {
									masterAddr  : this.baseJs.masterIP,
									version     : obj.version,
									redisAddr   : obj.redisAddr,
									redisPwd    : obj.redisPwd,
									controllerId: obj.cid,
									scryptUrl: obj.scryptUrl
								}
							}
						});
			},
			// 删除控制器
			delCont: function(obj){
				var _this = this;
				_this.$Spin.show();
				// var ws =new WebSocket(`ws://${sessionStorage.controlIP}:7200/test/ws/0120508cd011000120808031086a09ab50212040807100012`);
				var ws = new WebSocket(_this.wsUrl);
				ws.onopen = function () {
					ws.send(_this.wsSendData(obj,209));
				};
				ws.onmessage = function (result) {
					var data = JSON.parse(result.data);
					if(data.code == '209'){
						var sendData = {id: _this.opID}
						if(data.data.indexOf('大吉大利')>-1){
							_this.axios.post(`${_this.baseJs.postUrl}/control/delete`,sendData).then(function(response){
								var result = response.data;
								if(result.result){
									_this.$Spin.hide();
									_this.$Modal.remove();
									_this.$Message.success('操作成功！');
									_this.getContList(_this,_this.current);
								}else{
									_this.$Spin.hide();
									_this.$Modal.remove();
									_this.$Message.error(result.msg);
								}
							});
							ws.close();
						}else{
							_this.axios.post(`${_this.baseJs.postUrl}/control/delete`,sendData).then(function(response){
								var result = response.data;
								if(result.result){
									_this.$Spin.hide();
									_this.$Modal.remove();
									_this.$Message.success('操作成功！');
									_this.getContList(_this,_this.current);
								}else{
									_this.$Spin.hide();
									_this.$Modal.remove();
									_this.$Message.error(result.msg);
								}
							});
							ws.close();
						}
					};
				};
			},
			pageGo: function(e){},
			// 更新控制器
			updateCont: function(obj){
				var _this = this;
				_this.$Spin.show();
				var ws = new WebSocket(_this.wsUrl);
				ws.onopen = function () {
					ws.send(_this.wsSendData(obj,208));
				};
				ws.onmessage = function (result) {
					var data = JSON.parse(result.data);
					if(data.code == '208'){
						if(data.data.indexOf('大吉大利')>-1){
							_this.$Message.success('更新成功！');
							_this.$Spin.hide();
							ws.close();
						}else{
							_this.$Message.error('更新失败！');
							_this.$Spin.hide();
							ws.close();
						}
					}
				};
			},
			// 选择控制器IP
			contIPChange: function(e){
				var _this = this;
				_this.aeContProp.controlIP = e.label;
				_this.aeContProp.serverList.forEach(function(item,index){
					if(item.id == e.value){
						_this.aeContProp.propObj.privatekey = item.privatekey;
						_this.aeContProp.propObj.accountName = item.accountName;
						_this.aeContProp.propObj.psw = item.psw;
					}
				})
				
			},
			// 选择版本号
			versionChange: function(e){
				this.aeContProp.version = e.label;
			},
		}
    }
</script>


