<template>
    <div class="main-wrap">
        <div class="main-box">
			<div class="con-body">
                <div class="table-body">
                    <Table :columns="sockCol" :data="sockList"></Table>
                </div>
                <div class="table-footer">
                    <Row>
                        <Col span="10">
                            <Button type="primary" @click="openAeSock">创建sock</Button>
                            <a :href="`${this.baseJs.postUrl}/sock5/upload`"><Button type="primary">下载模板</Button></a>
                            <Upload :show-upload-list="false" :action="`${this.baseJs.postUrl}/sock5/inport`" style="width: 75px;display:inline-block;" :on-success="uploadSuccess" :on-error="uploadError"><Button type="primary" icon="ios-cloud-upload-outline">导入</Button></Upload>
                        </Col>
                        <Col span="14" style="text-align:right;"></Col>
                    </Row>
                </div>
            </div>
        </div>
        <!--S 创建sock-->
        <Modal v-model="aeSockProp.show" :title="aeSockProp.isEdit?'编辑SOCK':'创建SOCK'" :width="400" :mask-closable="false">
            <Form ref="aeSockForm" :model="aeSockProp.propObj" :rules="aeSockRule" :label-width="100">
				<FormItem prop="area" label="地区">
					<Input type="text" v-model="aeSockProp.propObj.area" placeholder="请输入内容..."></Input>
				</FormItem>
                <FormItem prop="sock5Addr" label="IP地址">
					<Input type="text" v-model="aeSockProp.propObj.sock5Addr" placeholder="请输入内容..."></Input>
				</FormItem>
                <FormItem prop="sock5Usr" label="用户名">
					<Input type="text" v-model="aeSockProp.propObj.sock5Usr" placeholder="请输入内容..."></Input>
				</FormItem>
                <FormItem prop="sock5Pwd" label="密码">
					<Input type="text" v-model="aeSockProp.propObj.sock5Pwd" placeholder="请输入内容..."></Input>
				</FormItem>
			</Form>
            <div slot="footer">
                <Button type="primary" @click="smAeSock">确定</Button>
            </div>
        </Modal>
        <!--E 创建sock-->
    </div>
</template>

<script>
export default {
    name: 'name',
    data: function() {	
        return {
            opID: null,
            total: null,
            current: 1,
            sockCol: [
                {title: '地区',key: 'area'},
                {title: 'IP:Port',key: 'sock5Addr'},
                {title: '用户名',key: 'sock5Usr'},
                {title: '密码',key: 'sock5Pwd'},
                {title: '绑定终端ID',key: '_id'},
                {
                    title: '操作',key: 'op',align: 'center',
                    render: (h, params) => {
                        var _this = this;
                        return h('div', [
                            h('Button',{
                                props: {
                                    type: 'ghost',
                                    size: 'small',
                                    icon: 'edit'
                                },
                                style: {
                                    marginRight: '5px'
                                },
                                on:{
                                    click: function(){
                                        _this.opID = params.row._id;
                                        _this.openAeSock('edit',params.row);
                                    }
                                }
                            },'编辑'),
                            h('Button',{
                                props: {
                                    type: 'error',
                                    size: 'small',
                                    icon: 'trash-a'
                                },
                                on:{
                                    click: function(){
                                        _this.opID = params.row._id;
                                        _this.$Modal.confirm({
                                            title: '谨慎操作',
                                            content: '<p>确定删除么？</p>',
                                            loading: true,
                                            onOk: () => {
                                                _this.delSock(_this.opID);
                                            }
                                        });
                                    }
                                }
                            },'删除')
                        ]);
                    }
                }
            ],
            sockList: [],
            aeSockRule: {
                area: this.baseJs.textRule,
                sock5Addr: this.baseJs.textRule,
            },
            aeSockProp: {
                show: false,
                isEdit: false,
                btnLoad: false,
                propObj: {
                    area: '',
                    sock5Addr: '',
                    sock5Usr: '',
                    sock5Pwd: '',
                },
            },
        }
    },
    created: function(){
        var _this = this;
        _this.getSockList(_this);
    },
    methods: {
        // 获取sock列表
        getSockList: function(){
            var _this = this;
            _this.axios.get(`${_this.baseJs.getUrl}/sock5s/ls`).then(function(response){
                var result = response.data
                if(result.result){
                    var dataList = result.data;
                    _this.sockList = dataList || [];
                }else{
                    _this.$Message.error(result.data);
                }
            })
        },
        // pageGo: function(e){
        //     var _this = this;
        //     _this.current = e;
        //     _this.getSockList(_this,e);
        // },
        openAeSock: function(edit,obj){
            var _this = this;
            _this.$refs['aeSockForm'].resetFields();
            if(edit == 'edit'){
                _this.aeSockProp.isEdit = true;
                _this.aeSockProp.propObj.area = obj.area;
                _this.aeSockProp.propObj.sock5Addr = obj.sock5Addr;
                _this.aeSockProp.propObj.sock5Usr = obj.sock5Usr;
                _this.aeSockProp.propObj.sock5Pwd = obj.sock5Pwd;
                console.log(obj);
            }else{
                _this.aeSockProp.isEdit = false;
            }
            _this.aeSockProp.show = true;
        },
        // 添加/编辑--提交sock
        smAeSock: function(){
            var _this = this;
				_this.$refs['aeSockForm'].validate((valid) => {
                    if (valid) {
                        _this.aeSockProp.btnLoad = true;
                        var url = '';
						var sendData = _this.baseJs.copyObj(_this.aeSockProp.propObj);
						if(_this.aeSockProp.isEdit){
							sendData._id = _this.opID;
							url = '/sock5s/update'
						}else{
							url = '/sock5s/create'
						}
                        _this.axios.post(`${_this.baseJs.postUrl}${url}`,sendData).then(function(response){
							var result = response.data;
							if(result.result){
								_this.aeSockProp.btnLoad = false;
								_this.aeSockProp.show = false;
								_this.$Message.success('操作成功！');
								_this.getSockList();
							}else{
								_this.aeSockProp.btnLoad = false;
								_this.$Message.error(result.data);
							}
						});
                    } else {
                        _this.$Message.error('请完善表单');
                    }
                })
        },
        // 删除服务器
        delSock: function(id){
            var _this = this;
            var sendData = {_id: id}
            _this.axios.post(`${_this.baseJs.postUrl}/sock5s/delete`,sendData).then(function(response){
                var result = response.data;
                if(result.result){
                    _this.$Modal.remove();
                    _this.$Message.success('操作成功！');
                    _this.getSockList(_this,_this.current);
                }else{
                    _this.$Modal.remove();
                    _this.$Message.error(result.msg);
                }
            });
        },
        // 文件导入成功
        uploadSuccess: function(e){
            var _this = this;
            if(e.result){
                _this.$Message.success(e.msg);
                _this.getSockList(_this,_this.current);
            }else{
                _this.$Message.error(e.msg);
            }
        },
        uploadError: function(e){
            this.$Message.error('上传失败，请重试!');
        }
    }
}
</script>

<style scoped>

</style>


