<style scoped>
.table-body{top: 100px;bottom:20px;border:none;}
.tabs-header{border-bottom: 1px solid rgb(221, 222, 225);}
.tabs-tab{cursor:pointer;margin: 0;margin-right: 4px;height: 31px;padding: 5px 16px 4px;border: 1px solid #dddee1;border-bottom: 0;border-radius: 4px 4px 0 0;-webkit-transition: all 0.3s ease-in-out;transition: all 0.3s ease-in-out;background: #f8f8f9;    display: inline-block;}
.tabs-tab.tabs-tab-active{height: 32px;padding-bottom: 5px;background: #fff;-webkit-transform: translateZ(0);transform: translateZ(0);border-color: #dddee1;color: #ff9900;}
.tabs-tab:hover{color:#ff9900;}
.tabs-content{height:100%;}
</style>
<template>
    <div class="main-wrap">
        <div class="main-box">
			<div class="con-body">
				<div class="top-back"><router-link :to="{ name: 'controllerManage'}"><Button type="primary" icon="chevron-left">返回</Button></router-link></div>
				<div class="tabs-header">
					<div class="tabs-tab" :class="{'tabs-tab-active':$route.path.indexOf('termList')>-1}" @click="tabSel('termList')">终端列表</div>
					<div class="tabs-tab" :class="{'tabs-tab-active':$route.path.indexOf('apiTest')>-1}"  @click="tabSel('apiTest')">接口测试</div>
					<div class="tabs-tab" :class="{'tabs-tab-active':$route.path.indexOf('log')>-1}" @click="tabSel('log')">日志</div>
				</div>
				<div class="table-body">
					<div class="tabs-content">
						<router-view></router-view>
					</div>
				</div>
			</div> 
        </div>
    </div>
</template>
<script>
    export default {
		data: function(){
			return {
				tabName: 'termList',
			}
		},
		created(){
			var _this = this;
		},
		methods: {
			// 切换选项卡
			tabSel: function(e){
				this.tabName = e;
				this.$router.push(`/${e}`);
			}
		}
    }
</script>


