<template>
<div class="top-bar">
    <div class="user">
    </div>
</div>
</template>

<script>
import photoUrl from '@/assets/side_logo.png'
export default {
    name: 'topbar',
    data: function() {
        return {
            photoUrl: photoUrl
        }
    },
    methods: {}
}
</script>

<style scoped>
.top-bar {
    overflow: hidden;
}

.user {
    width: 200px;
    height: 100%;
    text-align: right;
    float: right;
    line-height: 60px;
    font-size: 14px;
}
.user a{
    color: #3CAF36;
    letter-spacing: 1px;
}
.user-name {
    display: inline-block;
    width: 80px;
    word-break: keep-all;
    white-space: nowrap;
    vertical-align: middle;
    overflow: hidden;
    text-overflow: ellipsis;
    text-align: right;
}
</style>


