<template>
    <div class="wrap">
        <div class="col-con">
            <div class="request">
                <h2>Request</h2>
                <div class="item">
                    <span class="label">code ：</span>
                    <Select v-model="code">
                        <Option v-for="item in codeList" :value="item.value" :key="item.value">{{item.label}}</Option>
                    </Select>
                </div>
                <div class="item">
                    <span class="label">robot：</span>
                    <Select v-model="robot">
                        <Option v-for="item in robotList" :value="item.value" :key="item.value">{{item.label}}</Option>
                    </Select>
                </div>
                <div class="item">
                    <span class="label">data ：</span>
                    <i-input type="textarea" :rows="7" placeholder='{"key" : value }' v-model="dataCon"></i-input>
                </div>
                <div class="item" style="text-align: right;"><i-button type="primary" @click="submit">提交</i-button></div>
            </div>
            <div class="response">
                <h2>Response</h2>
                <span v-if="load">Loading...</span>
                <div class="response-body">{{responseCon}}</div>
            </div>
        </div>
    </div>
</template>

<script>
export default {
    name: 'name',
    data: function() {	
        return {
            ws:new WebSocket(`ws://${sessionStorage.controlIP}:7200/test/ws/0120508cd011000120808031086a09ab50212040807100012`),
            // ws:new WebSocket(this.baseJs.wsUrl),
            load: false,
            robot: '',
            robotList: [],
            code: null,
            codeList: [
                // {label:'握手-(100)-Code_HandShake',value: 100},
                // {label:'心跳-(101)-Code_HeartBeat',value: 101},
                {label:'机器人列表-(102)-Code_Robots',value: 102},
                {label:'微信状态-(103)-Code_WxStatus',value: 103},
                {label:'重置-(104)-Code_Reset',value: 104},
                {label:'二维码登录-(105)-Code_QrcodeLogin',value: 105},
                // {label:'二维码图片-(106)-Code_QrcodeImgBuf',value: 106},
                // {label:'二维码状态-(107)-Code_QrcodeStatus',value: 107},
                {label:'用户密码登录-(108)-Code_UsrPwdLogin',value: 108},
                // {label:'手机登录-(109)-Code_PhoneLogin',value: 109},
                {label:'二次登录-(110)-Code_SecondAuthLogin',value: 110},
                // {label:'-(111)-Code_InitWeixinData',value: 111},
                {label:'用户列表-(112)-Code_UsernameLists',value: 112},
                // {label:'异步消息-(113)-Code_AsyncMessage',value: 113},
                {label:'通讯录-(114)-Code_Contacts',value: 114},
                {label:'发送文本消息-(115)-Code_SendMsg',value: 115},
                {label:'发送图片消息-(116)-Code_SendMsgImage',value: 116},
                {label:'发送名片消息-(117)-Code_SendCardMsg',value: 117},
                {label:'发送语音消息-(118)-Code_SendVoiceMsg',value: 118},
                {label:'群发文本消息-(119)-Code_MessSendMsg',value: 119},
                {label:'群发语音消息-(120)-Code_MessSendVoice',value: 120},
                {label:'群发图片消息-(121)-Code_MessSendImage',value: 121},
                {label:'发送APP消息-(122)-Code_SendAppMsg',value: 122},
                {label:'下载图片-(123)-Code_DownloadImage',value: 123},
                {label:'下载语音-(124)-Code_DownloadVoice',value: 124},
                // {label:'创建红包-(125)-Code_CreatHongBao',value: 125},
                // {label:'创建红包-(126)-Code_TenPayRequest ',value: 126},
                {label:'-(127)-Code_OptionLog',value: 127},
                {label:'-(128)-Code_SearchContact',value: 128},
                {label:'-(129)-Code_GetUserByWxid',value: 129},
                {label:'-(130)-Code_UserOptionLog',value: 130},
                {label:'更改密码-(131)-Code_ChangePassword',value: 131},
                {label:'取消用户-(132)-Code_CancelUser',value: 132},
                {label:'更新头像-(133)-Code_UploadHeadImg',value: 133},
                {label:'设置身份信息-(133)-Code_SetPersonInfo',value: 134},
                {label:'设置被加方式-(135)-Code_SetAddFrom',value: 135},
                {label:'设置用户名-(136)-Code_SetWeixinUsername',value: 136},
                {label:'绑定手机-(137)-Code_BindMobile',value: 137},
                {label:'绑定邮箱-(138)-Code_BindEail',value: 138},
                {label:'创建群-(139)-Code_CreateChatRoom',value: 139},
                {label:'更改群名-(140)-Code_ChangeChatRoomName',value: 140},
                {label:'邀请群聊-(141)-Code_InviteChatRoom',value: 141},
                {label:'退出群-(142)-Code_ExitChatRoom',value: 142},
                {label:'踢群用户-(143)-Code_DelChatRoomUser',value: 143},
                {label:'获取群二维码-(144)-Code_GetRoomQrcode',value: 144},
                {label:'获取群信息-(145)-Code_GetRoomDetail',value: 145},
                {label:'添加群成员-(146)-Code_AddChatRoomMember',value: 146},
                {label:'关闭群验证-(147)-Code_ChatRoomVerifyOnOff',value: 147},
                {label:'允许加入群-(148)-Code_ApproveAddChatroomMember',value: 148},
                {label:'设置群公告-(149)-Code_SetChatRoomAnnouncement',value: 149},
                {label:'附近的人-(150)-Code_NearPeople',value: 150},
                {label:'摇一摇-(151)-Code_Yao1Yao',value: 151},
                {label:'-(152)-Code_SnsPost',value: 152},
                {label:'-(153)-Code_SnsTimeLine',value: 153},
                {label:'-(154)-Code_SnsObjectOp',value: 154},
                {label:'-(155)-Code_SnsUserPage',value: 155},
                {label:'-(156)-Code_SnsUploadImg',value: 156},
                {label:'-(157)-Code_SnsComment',value: 157},
                {label:'-(158)-Code_GetA8Key',value: 158},
                // {label:'(控制器)日志信息-(200)-Controller_LogMsg',value: 200},
                {label:'(控制器)版本号-(201)-Controller_Version',value: 201},
                {label:'(控制器)安装终端-(202)-Controller_SetupRobot',value: 202},
                {label:'(控制器)更新终端-(203)-Controller_UpdateRobot',value: 203},
                {label:'(控制器)删除终端-(204)-Controller_DeleteRobot',value: 204},
            ],
            dataCon: '',
            responseCon: '',
        }
    },
    created: function(){
        var _this = this;
        _this.ws.onopen = function () {};
        _this.ws.onmessage = function (result) {
			var data = JSON.parse(result.data);
			// 获取机器人列表
			if(data.code == 102){
				var tempList = [];
				data.data.forEach(function(item,index){
					tempList.push({
						value : item.exdata.robotId,
						label : item.exdata.robotName
					});
				})
				_this.robotList = tempList;
			};
			if(data.code == _this.code){
				_this.load = false;
				_this.responseCon = data;
			}
		}
    },
    methods: {
        // 提交测试
        submit: function(){
            var _this = this;
            _this.load = true;
            _this.responseCon = '';
            var sendData = JSON.stringify({
                code: _this.code,
                robotId: _this.robot,
                data: _this.dataCon?JSON.parse(_this.dataCon):''
            });
            _this.ws.send(sendData);
        },
    },
    beforeDestroy: function(){
        this.ws.close();
    }
}
</script>

<style scoped>
    .wrap{height: 100%;}
    .ivu-col{padding-top: 10px;}
    .ivu-col:first-child{padding-right: 10px;}
    .col-con{border: 1px solid rgb(221, 222, 225);padding: 20px;}
    .item{margin-bottom: 20px;}
    .label{font-size: 16px;font-weight: bold;}
    .response{border-top: 2px solid #ccc;margin-top: 20px;padding-top: 20px;}
    .response-body{height: 100%;height: 200px;border: 1px solid #ccc;overflow: auto;background:#f7f7f9;border-radius: 10px;padding: 10px;word-wrap:break-word;}
</style>


