import Vue from 'vue'
import Vuex from 'vuex'
import axios from 'axios'
import VueAxios from 'vue-axios'
import baseJs from '../js/base'
Vue.use(Vuex)
const getList = function(url){
	return new Promise(function(resolve){
		axios.get(`${baseJs.getUrl}${url}`).then(function(response){
			var result = response.data
			if(result.result){
				var dataList = result.data || [];
				resolve(dataList);
			}
		})
	})
}
const state = {
	adminName   : 0,
	sockList    : [],
	platformList: [],
	versionList : [],
	serverList  : [],
	areaList    : [],
};
const mutations = {
	setState: function(state,n){
		return state[n.name] = n.data
	},
};
const actions = {
	// 获取SOCK5列表
	getSockList: function(context){
		getList('/sock5s/ls').then(function(result){
			context.commit('setState',{name:'sockList',data: result});
		})
	},
	// 获取平台列表
	getPlatFormList: function(context){
		getList('/platforms/ls').then(function(result){
			context.commit('setState',{name:'platformList',data: result});
		})
	},
	// 获取版本列表
	getversionList: function(context){
		getList('/versions/ls').then(function(result){
			context.commit('setState',{name:'versionList',data: result});
		})
	},
	// 获取服务器列表
	getServerList: function(context){
		getList('/servers/ls').then(function(result){
			context.commit('setState',{name:'serverList',data: result});
		})
	},
	// 获取控制器类型列表
	getareaList: function(context){
		getList('/areas/ls').then(function(result){
			context.commit('setState',{name:'areaList',data: result});
		})
	}
}
export default new Vuex.Store({
	state,
	actions,
	mutations
})


