import Vue from 'vue'
import VueAxios from 'vue-axios'
import axios from 'axios'
import iView from 'iview'
import VueRouter from 'vue-router'
import App from './App.vue'
import 'iview/dist/styles/iview.css'
import './style/base.css'
import './theme/index.less'
import routes from './router'
import NavSide from './components/common/sidebar.vue'
import TopBar from './components/common/topBar.vue'
import baseJs from './js/base.js'
import 'babel-polyfill'
import store from './store/index.js'
//绑定basejs到vue原型
Vue.prototype.baseJs = baseJs
Vue.component(NavSide.name, NavSide);
Vue.component(TopBar.name, TopBar);
Vue.use(VueRouter)
Vue.use(VueAxios, axios)
Vue.use(iView)
//路由实例
const router = new VueRouter({
  routes
})
//判断是否登录
router.beforeEach(function (to, from, next) {
	var accessToken = sessionStorage.getItem('accessToken');
	if ((accessToken == "false" || !accessToken) && to.name != 'login') {
		next({
			path: '/login',
			query: {
				redirect: to.fullPath
			}
		})
	} else {
		next();
	}
});
new Vue({
	el: '#app',
	store,
	router,
	render: h => h(App)
})



