<template>
    <div class="main-wrap">
        <div class="main-box">
			<div class="con-body">
				<div class="tab">
					<ul>
						<li :class="{'active':tabName=='all'}" @click="tabName = 'all'">全部</li>
						<li :class="{'active':tabName=='controller'}" @click="tabName = 'controller'">控制器</li>
						<li :class="{'active':tabName=='term'}" @click="tabName = 'term'">终端</li>
						<li :class="{'active':tabName=='gameApi'}" @click="tabName = 'gameApi'">游戏API</li>
					</ul>
				</div>
				<div class="table-body">
					<div v-if="tabName=='all'"><server-list></server-list></div>
					<div v-if="tabName=='controller'"><controller-list></controller-list></div>
					<div v-if="tabName=='term'"><term-list></term-list></div>
					<div v-if="tabName=='gameApi'"><game-list></game-list></div>
				</div>
			</div> 
        </div>
    </div>
</template>
<script>
	import serverList from './serverList.vue'
	import controllerList from './controllerList.vue'
	import termList from './termList.vue'
	import gameList from './gameList.vue'
    export default {
		components: {
			serverList,
			controllerList,
			termList,
			gameList
		},
		data: function(){
			return {
				tabName: 'all',
			}
		},
		created(){
			var _this = this;
		},
		methods: {
		}
    }
</script>
<style scoped>
.con-body{overflow:auto;}
.table-body{top: 80px;bottom: 20px;}
.tab ul{border-bottom: 1px solid #eee;overflow: hidden;}
.tab li{float:left;width: 100px;text-align:center;margin-right: 10px;line-height: 35px;border-radius: 5px 5px 0 0;border-color:#eee;border-width: 1px 1px 0 1px;border-style: solid;cursor: pointer;}
.tab li:hover,.tab li.active{background: #ff9900;color:#fff;}
</style>


