<style scoped>
.con-body{overflow:auto;}
</style>
<template>
    <div class="main-wrap">
        <div class="main-box">
			<div class="con-body">
				<div class="table-body">
					<Table :columns="contCol" :data="contList"></Table>
				</div>
				<div class="table-footer">
					<Row>
						<Col span="4">
							<Button type="primary" @click="openAeAreaForm">添加地区</Button>
						</Col>
						<!--
						<Col span="20" style="text-align:right;">
							<Page :total="total" :current="current" @on-change="pageGo"></Page>
						</Col>
						-->
					</Row>
				</div>
			</div> 
        </div>
		<!--S 创建地区-->
		<Modal v-model="aeAreaProp.show" :title="aeAreaProp.isEdit?'编辑控制器类型':'创建控制器类型'" :width="400" :mask-closable="false">
			<Form ref="aeAreaForm" :model="aeAreaProp.propObj" :rules="aeAreaRule" :label-width="110">
				<FormItem prop="name" label="地区名称">
					<Input type="text" v-model="aeAreaProp.propObj.name" placeholder="请输入内容..."></Input>
				</FormItem>
				<FormItem prop="cdnId" label="cdnId">
					<Input type="text" v-model="aeAreaProp.propObj.cdnId" placeholder="请输入内容..."></Input>
				</FormItem>
				<FormItem prop="cdnServer" label="cdnServer">
					<Input type="text" v-model="aeAreaProp.propObj.cdnServer" placeholder="请输入内容..."></Input>
				</FormItem>
				<FormItem prop="cdnCode" label="cdnCode">
					<Input type="text" v-model="aeAreaProp.propObj.cdnCode" placeholder="请输入内容..."></Input>
				</FormItem>
				<FormItem prop="cdnKey" label="cdnKey">
					<Input type="text" v-model="aeAreaProp.propObj.cdnKey" placeholder="请输入内容..."></Input>
				</FormItem>
			</Form>
			<div slot="footer">
				<Button type="primary" @click="smAeArea" :loading="aeAreaProp.btnLoad">提交</Button>
			</div>
		</Modal>
		<!--E 创建地区-->
    </div>
</template>
<script>
    export default {
		data: function(){
			return {
				opID: null,
				total: null,
				current: 1,
				contCol: [
					{title: '地区名称',key: 'name'},
					{title: 'CDNserver',key: 'cdnServer'},
					{title: 'CDNid',key: 'cdnId'},
					{title: 'CDNcode',key: 'cdnCode'},
					{title: 'CDNkey',key: 'cdnKey'},
					{title: '操作',key: 'op',width: 170,align: 'center',
						render: (h, params) => {
                            var _this = this;
                            return h('div', [
                                h('Button',{
                                    props: {
                                        type: 'ghost',
                                        size: 'small',
                                        icon: 'edit'
                                    },
                                    style: {
                                        marginRight: '5px'
                                    },
                                    on:{
                                        click: function(){
											_this.opID = params.row._id;
											_this.openAeAreaForm('edit',params.row);
                                        }
                                    }
                                },'编辑'),
                                h('Button',{
                                    props: {
										disabled: params.row.status==1?true:false,
                                        type: 'error',
                                        size: 'small',
                                        icon: 'trash-a'
                                    },
                                    on:{
                                        click: function(){
											_this.opID = params.row._id;
											_this.$Modal.confirm({
												title: '谨慎操作',
												content: '<p>确定删除么？</p>',
												loading: true,
												onOk: () => {
													_this.delArea(_this.opID);
												}
											});
                                        }
                                    }
                                },'删除'),
                            ]);
                        }
					}
				],
				contList: [],
				aeAreaRule: {
					name: this.baseJs.textRule,
				},
				aeAreaProp: {
					show: false,
					btnLoad: false,
					isEdit: false,
					cdnList: [],
					propObj: {
						name: '',
						cdnId: '',
						cdnServer: '',
						cdnCode: '',
						cdnKey: '',
					}
				},
			}
		},
		created(){
			var _this = this;
			_this.getAreaList();
		},
		methods: {
			// 获取地区列表
			getAreaList: function(){
				var _this = this;
				_this.axios.get(`${_this.baseJs.getUrl}/areas/ls`).then(function(response){
					var result = response.data;
					if(result.result){
						var dataList =  result.data || [];
						_this.contList = dataList;
						_this.$store.commit('setState',{name:'areaList',data: dataList});
					}else{
						_this.$Message.error(result.data);
					}
				});
				
			},
			// 打开添加/编辑地区
			openAeAreaForm: function(edit,obj){
				var _this = this;
				_this.$refs['aeAreaForm'].resetFields();
				if(edit == 'edit'){
					this.aeAreaProp.isEdit = true;
					_this.aeAreaProp.propObj.name = obj.name;
					_this.aeAreaProp.propObj.cdnId = obj.cdnId;
					_this.aeAreaProp.propObj.cdnServer = obj.cdnServer;
					_this.aeAreaProp.propObj.cdnCode = obj.cdnCode;
					_this.aeAreaProp.propObj.cdnKey = obj.cdnKey;
				}else{
					this.aeAreaProp.isEdit = false;
				}
				this.aeAreaProp.show = true;
			},
			// 提交添加/编辑地区
			smAeArea: function(){
				var _this = this;
				_this.$refs['aeAreaForm'].validate((valid) => {
                    if (valid) {
						_this.aeAreaProp.btnLoad = true;
						var url = '';
						var sendData = _this.baseJs.copyObj(_this.aeAreaProp.propObj);
						if(_this.aeAreaProp.isEdit){
							sendData._id = _this.opID;
							url = '/areas/update'
						}else{
							url = '/areas/create'
						}
						_this.axios.post(`${_this.baseJs.postUrl}${url}`,sendData).then(function(response){
							var result = response.data;
							if(result.result){
								_this.aeAreaProp.btnLoad = false;
								_this.aeAreaProp.show = false;
								_this.$Message.success('操作成功！');
								_this.getAreaList();
							}else{
								_this.aeAreaProp.btnLoad = false;
								_this.$Message.error(result.data);
							}
						});
					}else{
						_this.$Message.error('请完善表单');
					}
				})
			},
			// 删除地区
			delArea: function(id){
				var _this = this;
				var sendData = {_id: id}
				_this.axios.post(`${_this.baseJs.postUrl}/areas/delete`,sendData).then(function(response){
					var result = response.data;
					if(result.result){
						_this.$Modal.remove();
						_this.$Message.success('操作成功！');
						_this.getAreaList();
					}else{
						_this.$Modal.remove();
						_this.$Message.error(result.data);
					}
				});
			},
		}
    }
</script>


