<template>
    <div style="height: 100%;">
        <div class="col-con" style="padding:0;border:none;height: 100%;position: relative;">
            <Menu mode="horizontal" theme="dark" active-name="all" @on-select="selectType">
                <MenuItem name="all">全部日志</MenuItem>
                <MenuItem name="controller">控制器日志</MenuItem>
                <MenuItem name="termAll">终端日志</MenuItem>
            </Menu>
            <div class="log-box" v-html="logCon" ref="logCon">
            </div>
        </div>
    </div>
</template>

<script>
export default {
    name: 'name',
    data: function() {	
        return {
            ws:new WebSocket(`ws://${sessionStorage.controlIP}:7200/test/ws/0120508cd011000120808031086a09ab50212040807100012`),
            logType: 'all',
            logCon: '',
        }
    },
    created: function(){
        var _this = this;
        _this.ws.onopen = function () {};
        _this.ws.onmessage = function (result) {
			var data = JSON.parse(result.data);
            var logCon = `时间：<b>${_this.baseJs.formatTime(data.timestamp*1000)}</b><br />code：【${data.code}】<br />data：${JSON.stringify(data).replace(/\n/g, '<br />')}<br />=================================<br />`;
            switch (_this.logType) {
                case 'all':
                    _this.logCon += logCon; 
                    break;
                case 'controller':
                    if(data.code >= 200){
                        _this.logCon += logCon;        
                    }
                    break;
                case 'termAll':
                    if(data.code < 200){
                        _this.logCon += logCon;        
                    }
                    break;
            }
            _this.$nextTick(function () {
                _this.$refs.logCon.scrollTop = _this.$refs.logCon.scrollHeight;
			});
        }
    },
    methods: {
        // 选择日志类型
        selectType: function(n){
            this.logType = n;
        }
    },
    watch: {
        logType: function(){
            this.logCon = '';
        }
    },
    beforeDestroy: function(){
        this.ws.close();
    }
}
</script>

<style scoped>
.log-box{position:absolute;top: 60px;right:0;bottom:0;left:0;overflow: auto;word-wrap:break-word;padding: 10px;border: 1px solid rgb(221, 222, 225);}
</style>


