<style scoped>
.con-body{padding: 20px 0;}
.left-sidebar{width: 220px;float:left;border-right: 1px solid #e9eaec;height: 100%;position: relative;}
.left-sidebar ul{padding-bottom: 30px;overflow: auto;height: 100%;}
.left-sidebar li{padding-left:20px;cursor: default;line-height: 40px;cursor:pointer;}
.left-sidebar li:hover,.left-sidebar li.active{background:#f8f8f9;}
.left-sidebar i{cursor:pointer;font-size: 16px;}
.addPlatform{position:absolute;bottom: 0;left:20px;right:0;background:#fff;}
.platformOpBtn:hover{background:#ff9900;}
.platformOpBtn:hover i{color:#fff !important;}
</style>
<template>
    <div class="main-wrap">
		<!--S 添加/编辑平台-->
		<Modal v-model="aePlatformProp.show" :title="aePlatformProp.isEdit?'编辑平台':'添加平台'" :width="350" :mask-closable="false">
			<Form ref="aePlatformForm" :model="aePlatformProp.propObj" :rules="aePlatformRule" :label-width="100">
                <FormItem prop="name" label="平 台 名 称">
					<Input v-model="aePlatformProp.propObj.name" placeholder="请输入平台名"></Input>
                </FormItem>
				<FormItem prop="isGameApi" label="开启游戏API">
					 <RadioGroup v-model="aePlatformProp.propObj.isGameApi">
						<Radio :label="1">是</Radio>
						<Radio :label="0">否</Radio>
					</RadioGroup>
                </FormItem>
				<div v-if="aePlatformProp.propObj.isGameApi">
					<FormItem prop="loginUsr" label="登录账号">
						<Input v-model="aePlatformProp.propObj.loginUsr" placeholder="登录账号"></Input>
					</FormItem>
					<FormItem prop="loginPwd" label="登录密码">
						<Input v-model="aePlatformProp.propObj.loginPwd" placeholder="登录账号"></Input>
					</FormItem>
					<FormItem prop="secretKey" label="登录密钥">
						<Input v-model="aePlatformProp.propObj.secretKey" placeholder="登录账号"></Input>
					</FormItem>
					<FormItem prop="webPort" label="端口号">
						<Input v-model="aePlatformProp.propObj.webPort" placeholder="登录账号"></Input>
					</FormItem>
					<FormItem prop="webPort" label="选择服务器">
						<Select v-model="aePlatformProp.propObj.serverId">
							<Option v-for="item in serverFilter" :value="item._id" :key="item._id">{{item.name}}</Option>
						</Select>
					</FormItem>
				</div>
			</Form>
			<div slot="footer"><Button type="primary" @click="smPlatform" :loading="aePlatformProp.btnLoad">确定</Button></div>
		</Modal>
		<!--E 添加/编辑平台-->
		<!--S 查看游戏API-->
		<Modal v-model="gameApiProp.show" title="游戏API" :width="350">
			<div style="line-height: 30px;">
				<p>登录账号：{{opObj?opObj.loginUsr:''}}</p>
				<p>登录密码：{{opObj?opObj.loginPwd:''}}</p>
				<p>登录密钥：{{opObj?opObj.secretKey:''}}</p>
				<p>登录端口：{{opObj?opObj.webPort:''}}</p>
				<p>服务器ID：{{opObj?opObj.serverId:''}}</p>
			</div>
			<div slot="footer"></div>
		</Modal>
		<!--E 查看游戏API-->
        <div class="main-box">
			<div class="con-body">
				<div class="left-sidebar">
					<ul>
						<li v-for="item in platformList" @click="lookTermList(item)" :class="{'active':platformId==item._id?true:false}">
							<Row>
								<Col span="18" class="omit">{{item.name}}</Col>
								<Col span="3" class="tc platformOpBtn" @click.native.stop="openAePlatform('edit',item)"><Icon type="compose" title="编辑" color="#ff9900"></Icon></Col>
								<Col span="3" class="tc platformOpBtn" @click.native.stop="delPlatform(item._id)"><Icon type="trash-a" title="删除" color="#ff9900"></Icon></Col>
							</Row>
						</li>
					</ul>
					<div class="addPlatform"><Button type="primary" @click="openAePlatform">添加平台</Button></div>
				</div>
				<div style="margin-left: 220px;padding: 0 20px;position:relative;height: auto;">
					<div style="padding: 20px 0;font-size: 18px;font-weight:bold;color:#666;">
						<Row v-if="platformId">
							<Col span="16">
								<p style="font-size:16px;margin-top: 5px;">密钥: {{signKey}}<Button type="primary" size="small" icon="refresh" style="margin-left: 10px;" @click="updateKey">更新密钥</Button></p>
							</Col>
							<Col span="8" class="tr">
								<Button type="primary" v-if="opObj.isGameApi" @click="installGameApi">安装游戏API</Button>
								<Button type="ghost" v-if="opObj.isGameApi" @click="gameApiProp.show = true">查看游戏API</Button>
							</Col>
						</Row>
					</div>
					<div style="position:absolute;top:70px;right:20px;bottom:40px;left:20px;overflow:auto;">
						<Table :columns="termCol" :data="_termList" :loading="tableLoad"></Table>
						<div class="tc mt-20 mb-20" v-if="nextPageShow"><Button type="primary" @click="nextPage" :loading="nextPageLoad">加载更多</Button></div>
					</div>
				</div>
			</div> 
        </div>
    </div>
</template>
<script>
	import md5 from 'js-md5'
    export default {
		data: function(){
			return {
				opObj: null,
				opID: null,
				current: 1,
				searchKey: '',
				nextPageShow: false,
				nextPageLoad: false,
				platformId: '',
				signKey: '',
				contSel: '1',
				platformList: [],
				tableLoad: false,
				termCol: [
					{title: '终端ID',key: '_id'},
					{title: '终端名',key: 'name'},
					{title: '地区',key: 'area'},
					{title: '服务器IP',key: 'controllerIp'},
					{title: '服务器名',key: 'serverName'},
					{title: '类型',key: 'stype'},
					{title: 'SOCK5',key: 'sock5On'},
					{title: '操作',key: 'op',width: 150,align:'center',
						 render: (h, params) => {
							var _this = this;
							return h('div', [
								h('Button',{
									props: {
										type: 'primary',
										size: 'small',
									},
									on:{
										click: function(){
											_this.updateTerm(params.row);
										}
									}
								},'更新')
							])
						 }
					}
				],
				termList: [],
				aePlatformProp: {
					show: false,
					btnLoad: false,
					isEdit: false,
					propObj: {
						name: '',
						isGameApi: 0,
						loginUsr: '',
						loginPwd: '',
						secretKey: '',
						webPort: '',
						serverId: '',
					}
				},
				aePlatformRule: {
					name: this.baseJs.textRule
				},
				confTermProp: {
					load: false,
					show: false,
					btnLoad: false,
					contTypeList: [],
					contTypeID: null,
					contList: [],
					contID: null,
					termList: [],
					termIDList: [],
				},
				gameApiProp: {
					show: false,
				}
			}
		},
		created(){
			var _this = this;
			_this.getPlatformList();
		},
		computed: {
			_termList(){
				var arr = this.termList.filter(item=>{
					return (item.name == this.searchKey) || (item._id == this.searchKey);
				})
				if(this.searchKey){
					return arr;
				}else{
					return this.termList;
				}
			},
			serverList(){
				return this.$store.state.serverList;
			},
			serverFilter(){
				var arr = [];
				this.serverList.filter(item=>{
					if(item.type == '' || item.type == 'gameapi'){
						arr.push(item)
					}
				})
				return arr;
			},
		},
		methods: {
			// 获取平台列表
			getPlatformList: function(){
				var _this = this;
				_this.axios.get(`${this.baseJs.getUrl}/platforms/ls`).then(function(response){
					var result = response.data;
					if(result.result){
						var dataList = result.data || [];
						_this.platformList = dataList;
						_this.$store.commit('setState',{name:'platformList',data: dataList});
					}else{
						_this.$Message.error(result.data);
					}
				})
			},
			// 打开添加平台
			openAePlatform: function(edit,obj){
				var _this = this;
				_this.baseJs.resetForm(_this,'aePlatformForm');
				if(edit == 'edit'){
					_this.aePlatformProp.isEdit = true;
					_this.opID = obj._id;
					this.aePlatformProp.propObj.name = obj.name;
					this.aePlatformProp.propObj.isGameApi = obj.isGameApi?1:0;
					this.aePlatformProp.propObj.loginUsr = obj.loginUsr;
					this.aePlatformProp.propObj.loginPwd = obj.loginPwd;
					this.aePlatformProp.propObj.secretKey = obj.secretKey;
					this.aePlatformProp.propObj.webPort = obj.webPort;
					this.aePlatformProp.propObj.serverId = obj.serverId;
				}else{
					_this.aePlatformProp.isEdit = false;
					_this.aePlatformProp.propObj.loginUsr = 'root';
					_this.aePlatformProp.propObj.loginPwd = '123456';
					_this.aePlatformProp.propObj.secretKey = md5(this.baseJs.uuid());
					_this.aePlatformProp.propObj.webPort = this.baseJs.randomWebPort(6535,8000)+'';
				}
				_this.aePlatformProp.show = true;
			},
			// 提交添加、编辑平台
			smPlatform: function(){
				var _this = this;
					_this.$refs['aePlatformForm'].validate((valid) => {
                    	if (valid) {
							_this.aePlatformProp.btnLoad = true;
							var url = '';
							var sendData = _this.baseJs.copyObj(_this.aePlatformProp.propObj);
							if(_this.aePlatformProp.isEdit){
								sendData._id = _this.opID;
								url = '/platforms/update'
							}else{
								url = '/platforms/create'
								sendData.isGameApi?sendData.isGameApi = true:sendData.isGameApi = false;
							}
							_this.axios.post(`${_this.baseJs.postUrl}${url}`,sendData).then(function(response){
								var result = response.data;
								if(result.result){
									if(_this.opObj){
										if(_this.opObj._id == sendData._id){
											_this.opObj.serverId = _this.aePlatformProp.propObj.serverId;
										}
									}
									console.log(_this.opObj);
									_this.aePlatformProp.btnLoad = false;
									_this.aePlatformProp.show = false;
									_this.$Message.success('操作成功！');
									_this.getPlatformList();
								}else{
									_this.aePlatformProp.btnLoad = false;
									_this.$Message.error(result.data);
								}
							});
						}else{
							_this.$Message.error('请完善表单！');	
						}
					})
			},
			// 删除平台
			delPlatform: function(id){
				var _this = this;
				_this.$Modal.confirm({
					title: '谨慎操作',
					content: '<p>确定删除么？</p>',
					loading: true,
					onOk: () => {
						var sendData = {_id: id}
						_this.axios.post(`${this.baseJs.postUrl}/platforms/delete`,sendData).then(function(response){
							var result = response.data
							if(result.result){
								_this.$Modal.remove();
								_this.getPlatformList();
								if(_this.platformId == id){
									_this.platformId = null;
									_this.termList = [];
								}
								_this.$Message.success('删除成功!');
							}else{
								_this.$Modal.remove();
								_this.$Message.error(result.data);
							}
						})
					}
				});
			},
			// 查看终端列表
			lookTermList: function(obj){
				var _this = this;
				if(_this.platformId == obj._id){return;}
				_this.opID = obj._id;
				_this.opObj = obj;
				_this.platformId = obj._id;
				_this.signKey = obj.signKey;
				_this.tableLoad = true;
				_this.getTermList(obj._id,1);
			},
			//获取终端列表
			getTermList: function(id,page){
				var _this = this;
				if(page == 1){_this.current = 1;}
				_this.axios.get(`${_this.baseJs.getUrl}/robots/findarr/platformId/${id}?page=${page}`).then(function(response){
					var result = response.data;
					if(result.result){
						var dataList = result.data || [];
						if(dataList.length < 30){
							_this.nextPageShow = false;
						}else{
							_this.nextPageShow = true;
						}
						if(page == 1){
							_this.termList = dataList;
						}else{
							dataList.forEach(item=>{
								_this.termList.push(item)
							})
						}
						_this.tableLoad = false;
						_this.nextPageLoad = false;
					}else{
						_this.tableLoad = false;
						_this.nextPageLoad = false;	
					}
				});
			},
			// 更新终端
			updateTerm(obj){
				var _this = this;
				var sendData = {
					robotId: obj._id,
					serverId: obj.serverId,
					stype: obj.stype
				};
				_this.$Spin.show();
				_this.axios.post(`${_this.baseJs.postUrl}/remote/robot/update`,sendData).then(function(response){
					var result = response.data
					if(result.result){
						_this.$Spin.hide();
						_this.$Message.success('操作成功!');
					}else{
						_this.$Spin.hide();
						_this.$Message.error(result.data);
					}
				})
			},
			// 更新密钥
			updateKey: function(type){
				var _this = this;
				var uuid =  md5(_this.baseJs.uuid());
				var sendData = {
					_id: _this.platformId,
					signKey: uuid
				}
				_this.$Spin.show();
				_this.axios.post(`${_this.baseJs.postUrl}/platforms/update`,sendData).then(function(response){
					var result = response.data
					if(result.result){
						var dataList = result.data;
						_this.$Message.success('更新成功！');
						_this.getPlatformList();
						_this.signKey = uuid;
						_this.$Spin.hide();
					}else{
						_this.$Spin.hide();
						_this.$Message.error(result.data);
					}
				})
			},
			// 安装游戏API
			installGameApi(){
				var _this = this;
				var sendData = {
					serverId: this.opObj.serverId,
					platformId: this.opObj._id
				}
				_this.axios.post(`${_this.baseJs.postUrl}/remote/gameapi/setup`,sendData).then(function(response){
					var result = response.data
					if(result.result){
						console.log(result);
						_this.$Message.success(result.data);
					}else{
						_this.$Message.error(result.data);
					}
				})
			},
			// 加载更多
			nextPage(){
				this.current+=1;
				this.nextPageLoad = true;
				this.getTermList(this.platformId,this.current);
			},
		},
    }
</script>


